<template>
	<div id="nav" class="app">
		<Header @setting="settingView" />
		<router-view
			@send_new_message="sendMessage"
			:settings-view="settingsView"
			:user-data="userData"
			:chat-list="chatList"
			:websocket-message="webSocketMessages"
		/>
	</div>
</template>

<script>
import Header from "@/components/Header.vue";

export default {
	components: {
		Header,
	},
	data() {
		return {
			settingsView: false,
			ws: "",
			user_id: true,
			webSocketMessages: [],
			userData: {},
			chatList: [],
		};
	},
	beforeCreate() {
		if (!localStorage.getItem("token") && location.pathname != "/register") {
			this.$router.push({ path: "/auth" });
		}

		//Запорос на данные о пользователе
		var headers = new Headers();
		headers.append("Authorization", localStorage.getItem("token"));

		let requestOptions = {
			method: "GET",
			redirect: "follow",
			headers: headers,
		};

		fetch("https://diplom-api.na4u.ru/api/me", requestOptions)
			.then((response) => response.json())
			.then((result) => {
				console.log(result);
				this.userData = result.values[0];
				localStorage.setItem("uid", result.values[0].id);
			})
			.catch((error) => console.log("error", error));

		//запрос на список чатов пользователя

		fetch(`https://diplom-api.na4u.ru/api/chat/get`, {
			method: "GET",
			headers: headers,
		})
			.then((response) => response.json())
			.then((result) => {
				if (!result.values.message) {
					let values = result.values;
					this.chatList = values;
					console.log(this.chatList);
				} else this.chatList = false;
			})
			.catch((error) => console.log("error", error));
	},
	async mounted() {		
		this.ws = new WebSocket("wss://websocketchat.na4u.ru");
		// console.log(this.ws);

		this.ws.onopen = () => {
			console.log("[WS] - Connected.");
		};

		this.ws.onmessage = (event) => {
			let msgData = JSON.parse(event.data);

			let msgObj = {
				text: msgData.message,
				from_user_id: msgData.user_id,
				chat_id: msgData.chat_id,
			};

			if (Object.prototype.hasOwnProperty.call(msgData, "image"))
				msgObj["image"] = msgData.image;

			this.webSocketMessages.push(msgObj);

			document.getElementsByClassName('chat-area')[0].scrollTop = document.getElementsByClassName('chat-area-main')[0].scrollHeight * 999	

			setTimeout(()=>document.getElementsByClassName('chat-area')[0].scrollTop = document.getElementsByClassName('chat-area-main')[0].scrollHeight * 999, 500)
		};
	},
	methods: {
		settingView(data) {
			this.settingsView = data.settingView;
		},
		sendMessage: async function (data) {
			let sendObj = {
				message: data.message,
				user_id: data.user_id,
				chat_id: data.chat_id,
			};

			if (Object.prototype.hasOwnProperty.call(data, "image"))
				sendObj["image"] = data.image;

			this.ws.send(JSON.stringify(sendObj));

			document.getElementsByClassName('chat-area')[0].scrollTop = document.getElementsByClassName('chat-area-main')[0].scrollHeight * 999	

			setTimeout(()=>document.getElementsByClassName('chat-area')[0].scrollTop = document.getElementsByClassName('chat-area-main')[0].scrollHeight * 999, 100)
			
		},
	},
};
</script>

<style>
@import url(./assets/styles/main.css);
</style>
