<template>
  <div class="wrapper" v-if="renderComponent">
    <div class="conversation-area" :class="{active: mobileList}">
      <div class="empty" v-if="chatList">
        <div @click="openChat(item.id)" v-for="item in chatList" :key="item.id" class="msg active">
          <img class="msg-profile" v-if="item.interlocutor.profile_picture" :src="'https://diplom-api.na4u.ru/uploads/' + item.interlocutor.profile_picture.split('/').pop()" alt="">
          <img class="msg-profile" v-else src="https://diplom-api.na4u.ru/uploads/auto.jpg" alt="">
          <div class="msg-detail">
            <div class="msg-username">{{ item.interlocutor.login }}</div>
            <div class="msg-content">
              <span class="msg-message">Зайти в чат</span>
            </div>
          </div>
        </div>
      </div>
      <div class="answ" v-else>
        <p style="margin-top: 36px">У вас нет активных чатов</p>
      </div>
      <router-link to="/search" class="add"></router-link>
      <div class="overlay"></div>
    </div>
    <div class="chat-area">
        <div class="chat-area-header" v-if="chatList">
          <div class="swipe-chats" @click="viewMobileList(settingsView)">{{mobileListMessage}}</div>
          <div class="chat-area-title" v-if="chatId">{{ currentInterlocutor.login }}</div>
          <div class="chat-area-title" v-else>Перейдите в список ваших чатов</div>
          <div class="chat-area-group" v-if="chatId">
            <img class="chat-area-profile" v-if="currentInterlocutor.profile_picture" :src="'https://diplom-api.na4u.ru/uploads/' + currentInterlocutor.profile_picture.split('/').pop()" alt="">
            <img class="chat-area-profile" v-else src="https://diplom-api.na4u.ru/uploads/auto.jpg" alt="">
          </div>
        </div>
        <div class="chat-area-main">
          <div v-for="item in chatMessage" :key="item.id">
            <div v-if="item.from_user_id != user_id" class="chat-msg">
              <div class="chat-msg-content">
                <div class="chat-msg-text">
                  <img class="chat-msg-image" v-if="JSON.parse(item.text).image" :src="'https://diplom-api.na4u.ru/uploads/' + JSON.parse(item.text).image.split('/').pop()" alt="">
                  <p>{{ JSON.parse(item.text).message }}</p>
                </div>
              </div>
            </div>
            <div v-else class="chat-msg owner">
              <div class="chat-msg-content">
                <div class="chat-msg-text">
                  <img class="chat-msg-image" v-if="JSON.parse(item.text).image" :src="'https://diplom-api.na4u.ru/uploads/' + JSON.parse(item.text).image.split('/').pop()" alt="">
                  <p>{{ JSON.parse(item.text).message }}</p>
                </div>
              </div>
            </div>
          </div>
          <div v-for="item in websocketMessage" :key="item.id">
            <div v-if="item.chat_id === chat">
              <div v-if="item.from_user_id !== user_id" class="chat-msg">
                <div class="chat-msg-content">
                  <div class="chat-msg-text">
                    <img class="chat-msg-image" v-if="item.image" :src="'https://diplom-api.na4u.ru/uploads/' + item.image.split('/').pop()" alt="">
                    <p>{{ item.text }}</p>
                  </div>
                  
                </div>
              </div>
              <div v-if="item.from_user_id === user_id" class="chat-msg owner">
                <div class="chat-msg-content">
                  <div class="chat-msg-text">
                    <img class="chat-msg-image" v-if="item.image" :src="'https://diplom-api.na4u.ru/uploads/' + item.image.split('/').pop()" alt="">
                    <p>{{ item.text }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="chat-area-footer" v-if="chatId">
          <div v-if="preview_url" class="preview_image">
            <div class="close" @click="deleteImage">✘</div>
            <img :src="preview_url" alt="">
          </div>
          <input @change="previewImage" type="file" d="upload" ref="imageFile" hidden style="display: none"/>
          <svg @click="$refs.imageFile.click()" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" class="feather feather-paperclip">
          <path d="M21.44 11.05l-9.19 9.19a6 6 0 01-8.49-8.49l9.19-9.19a4 4 0 015.66 5.66l-9.2 9.19a2 2 0 01-2.83-2.83l8.49-8.48" /></svg>
          <input v-model="message" type="text" placeholder="Напишите сообщение..." />
          <svg @click="sendNewMessage" xmlns="http://www.w3.org/2000/svg" width="18" height="18" fill="#0086ff" class="bi bi-send" viewBox="0 0 16 16">
            <path d="M15.854.146a.5.5 0 0 1 .11.54l-5.819 14.547a.75.75 0 0 1-1.329.124l-3.178-4.995L.643 7.184a.75.75 0 0 1 .124-1.33L15.314.037a.5.5 0 0 1 .54.11ZM6.636 10.07l2.761 4.338L14.13 2.576 6.636 10.07Zm6.787-8.201L1.591 6.602l4.339 2.76 7.494-7.493Z"/>
          </svg>
        </div>
      </div>
      <div v-if="settingsView && settingsViewDetail" class="detail-area">
        <input @change="onFileUpload($event)" type="file" d="upload" ref="theFile" hidden style="display: none"/>
        <div class="profile_image">
          <div class="image_bg" @click="$refs.theFile.click()">
            <p>✎</p>
          </div>
          <img class="profile_image" v-if="profilepic" :src="'https://diplom-api.na4u.ru/uploads/' + profilepic" alt="">
        </div>
        <h2>{{ userData.login }}</h2>
        <hr>
        <h5>{{ userData.phone }}</h5>
        <button class="logout" @click="logout">Выйти</button>
      </div>
    </div>
</template>

<script>
    export default {
      props: [ 'settingsView', 'ws', 'websocketMessage' ],
      created() {
        window.addEventListener('keydown', (e) => {
          if (e.key == 'Enter') {
            this.sendNewMessage();
          }
        });
      },
      data() {
        return {
          chatId: localStorage.getItem("chat_id"),
          renderComponent: true,
          settingsViewDetail: true,
          mobileList: false,
          mobileListMessage: '←',
          currentInterlocutor: {},
          chatList: [],
          userData: [],
          profilepic: '',
          user_id: localStorage.getItem('uid'),
          chatMessage: {

          },
          chat: '',
          message: '',
          file: '',
          preview_url: ''
        }
      },
      beforeCreate() {
        var headers = new Headers();
        headers.append("Authorization", localStorage.getItem('token'));
        fetch(`https://diplom-api.na4u.ru/api/chat/get`, {method: 'GET', headers: headers})
        .then(response => response.json())
        .then(result => {
          if (!result.values.message) {
            let values = result.values;
            this.chatList = values;
            
            this.currentInterlocutor = values.find(function(v) { return v['id'] == localStorage.getItem('chat_id')}).interlocutor
          } else this.chatList = false
        })
        .catch(error => console.log('error', error));

        let requestOptions = {
            method: 'GET',
            redirect: 'follow',
            headers: headers
        };

        fetch("https://diplom-api.na4u.ru/api/me", requestOptions)
            .then(response => response.json())
            .then(result => {
                this.userData = result.values[0]
                if (result.values[0].profile_picture) this.profilepic = result.values[0].profile_picture.split('/').pop()
                else this.profilepic = 'auto.jpg'
            })
            .catch(error => console.log('error', error));
      },
      mounted() {
        if (localStorage.getItem('chat_id'))
          this.chat = localStorage.getItem('chat_id')

        var headers = new Headers();
        headers.append("Authorization", localStorage.getItem('token'));

        let requestOptions = {
          method: 'GET',
          headers: headers
        };

        if (localStorage.getItem('chat_id'))
          fetch(`https://diplom-api.na4u.ru/api/chat/messages?id=${localStorage.getItem('chat_id')}`, requestOptions)
              .then(response => response.json())
              .then(result => {
                console.log(result)
                if (result.values)
                  this.chatMessage = result.values
              })
              .catch(error => console.log('error', error));
        
        setTimeout(
          ()=>document.getElementsByClassName('chat-area')[0].scrollTop = document.getElementsByClassName('chat-area-main')[0].scrollHeight * 999,
          500
        )
      },
      methods: {
        deleteImage() {
          this.$refs.imageFile.value = null
          this.preview_url = ''
        },
        previewImage(e) {
          console.log(e.target.files);
          this.isUpload=true;
          let file = e.target.files[0];
          let url = '';
          var reader = new FileReader();
          reader.readAsDataURL(file);
          let that = this;
          reader.onload = function () {
            url=this.result.substring(this.result.indexOf(',')+1);
            that.preview_url = 'data:image/png;base64,'+url
          }
        },
        viewMobileList(setting) {
          this.mobileList = !this.mobileList;

          if(this.mobileList) {
            if(setting) {
              this.settingsViewDetail = false;
            } else {
              this.settingsViewDetail = false;
            }
            this.mobileListMessage = '✘'
          } else {
            if(setting) {
              this.settingsViewDetail = true;
            } else {
              this.settingsViewDetail = true;
            }
            this.mobileListMessage = '←'
          }
        },
        logout() {
          localStorage.removeItem('uid');
          localStorage.removeItem('token');
          localStorage.removeItem('chat_id');
          location.reload();
        },
        sendNewMessage() {
          this.preview_url = '';
          let obj = { message: this.message, user_id: localStorage.getItem('uid'), chat_id: localStorage.getItem('chat_id') }

          let file = this.$refs.imageFile.files[0];
          if (file) {
              var headers = new Headers();
              headers.append("Authorization", localStorage.getItem('token'));

              var formdata = new FormData();
              formdata.append("image", file, file.name);
              
              var requestOptions = {
                  method: 'POST',
                  headers: headers,
                  body: formdata,
                  redirect: "follow"
              };
  
              fetch("https://diplom-api.na4u.ru/api/chat/imageappend", requestOptions)
              .then(response => response.json())
              .then(result => {
                  if (Object.prototype.hasOwnProperty.call(result.values, 'imgpath')) {
                      obj['image'] = result.values.imgpath;
                      this.$emit('send_new_message', obj)
                  } else { if (this.message) this.$emit('send_new_message', obj) }
              }).catch(error => console.log('error', error));
          } else { if (this.message) this.$emit('send_new_message', obj) }

          console.log(this.websocketMessage);

          this.message = ''
          this.$refs.imageFile.value = null;
        },
        openChat(id) {		
          this.message = '';
          this.chat = id;
          localStorage.setItem('chat_id', this.chat);

          location.reload();
        },
        onFileUpload() {
          var headers = new Headers();
          headers.append("Authorization", localStorage.getItem('token'));

          var formdata = new FormData();
          formdata.append("image", this.$refs.theFile.files[0], this.$refs.theFile.files[0].name);

          var requestOptions = {
            method: 'POST',
            headers: headers,
            body: formdata,
            redirect: 'follow'
          };

          fetch("https://diplom-api.na4u.ru/api/profile/image", requestOptions)
            .then(response => response.json())
            .then(result => { console.log(result); location.reload(); })
            .catch(error => console.log('error', error));
        }
      }
    }
</script>
